
import { defineComponent } from "vue";
import { DCM20100112_RES, DCM20100112_REQ } from "@/shared/type/DCM20100112.type";
import { DCM20100141_RES, DCM20100141_REQ } from "@/shared/type/DCM20100141.type";
import { modalController } from "@ionic/vue";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";
import { screenMode } from "@/shared/type/Approval.type";
import { APM20200113_RES } from "@/shared/type/APM20200113.type";

export default defineComponent({
    name: "dcm201001sc01",
    components: {
        BreadCrumb,
        SelectOption
    },

    data() {
        const chequeRequestDetail = {
            chequeQty: "",
            feeAmount: "",
            currencyCode: "",
            visitBranchName: "",
            receiverContactNo: "",
            receiverName: "",
            sourceOfFundAccountNo: "",
            internetBankingApprovalNo: 0,
            checkBookProgressStatusCode: "",
            sourceOfFundAccountName: "",
            remark: "",
            receiverPositionTitle: "",
            receiverIDNo: "",
            receiverIDTypeCode: "",
            chequeBookQty: "",
        };

        return {
            chequeRequestDetail,
            isEditable: true,
            isInValidInput: false,
            isDisabledSelectRequest: false,
            isDisabledSelectProcessingAndRequest: false,
            operationMode: 'normal' as screenMode,
            approvalRes: {} as any
        };
    },

    async mounted() {
        if (this.approvalType === '') {
            this.requestChequeRequestDetail();
        } else {
            await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                const reqBody = {
                    internetBankingApprovalNo: Number(authDetailParsed.internetBankingApprovalNo),
                    approvalRequestDate: authDetailParsed.approvalRequestDate,
                } as DCM20100112_REQ;

                const res = await this.$request.getRequest("DCM20100112", reqBody, false) as DCM20100112_RES;
                this.approvalRes = authDetail.body
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
                this.chequeRequestDetail = {
                    chequeBookQty: res.body.chequeBookQty,
                    chequeQty: res.body.chequeQty,
                    feeAmount: this.$format.formatCurrency(res.body.feeAmount, res.body.currencyCode),
                    visitBranchName: res.body.visitBranchName,
                    receiverIDNo: res.body.receiverIDNo,
                    receiverName: res.body.receiverName,
                    sourceOfFundAccountNo: res.body.sourceOfFundAccountNo,
                    sourceOfFundAccountName: res.body.sourceOfFundAccountName,
                    currencyCode: res.body.currencyCode,
                    receiverContactNo: res.body.receiverContactNo,
                    receiverIDTypeCode: this.$codeConverter.convertValue("ReceiverIDTypeCode", res.body.receiverIDTypeCode),
                    receiverPositionTitle: res.body.receiverPositionTitle,
                    checkBookProgressStatusCode: authDetailParsed.checkBookProgressStatusCode,
                    internetBankingApprovalNo: authDetailParsed.internetBankingApprovalNo,
                    remark: authDetailParsed.remark
                };
            })
        }
    },

    computed: {
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },

        status(): any {
            return this.$store.state.CommonCodeModule.commonCode.Status
        },

        statusCodeValue() {
            return (statusCode: string, statusValue: string) => `${statusCode}` + ' : ' + `${statusValue}`;
        },

        statusCompleted(): boolean {
            if (this.chequeRequestDetail.checkBookProgressStatusCode === '03' || this.chequeRequestDetail.checkBookProgressStatusCode === '04' || this.chequeRequestDetail.checkBookProgressStatusCode === '09' || this.chequeRequestDetail.checkBookProgressStatusCode === '02') {
                return true
            }
            return false
        }
    },

    methods: {
        async requestChequeRequestDetail() {
            const reqBody = {
                internetBankingApprovalNo: Number(this.$route.query.internetBankingApprovalNo),
                approvalRequestDate: this.$route.query.approvalRequestDate,
            } as DCM20100112_REQ;
            console.log(reqBody);

            const res = (await this.$request.getRequest("DCM20100112", reqBody, false)) as DCM20100112_RES;
            if (res.header.result) {
                this.chequeRequestDetail = {
                    chequeBookQty: res.body.chequeBookQty,
                    feeAmount: this.$format.formatCurrency(res.body.feeAmount, res.body.currencyCode),
                    visitBranchName: res.body.visitBranchName,
                    receiverIDNo: res.body.receiverIDNo,
                    receiverName: res.body.receiverName,
                    sourceOfFundAccountNo: res.body.sourceOfFundAccountNo,
                    sourceOfFundAccountName: res.body.sourceOfFundAccountName,
                    chequeQty: res.body.chequeQty,
                    currencyCode: res.body.currencyCode,
                    internetBankingApprovalNo: res.body.internetBankingApprovalNo,
                    receiverContactNo: this.$format.formatPhoneNumber(res.body.receiverContactNo),
                    checkBookProgressStatusCode: res.body.checkBookProgressStatusCode,
                    remark: res.body.remark,
                    receiverIDTypeCode: this.$codeConverter.convertValue("ReceiverIDTypeCode", res.body.receiverIDTypeCode),
                    receiverPositionTitle: res.body.receiverPositionTitle,
                };
                this.isDisabledSelectRequest = res.body.checkBookProgressStatusCode === '01' ? true : false
                this.isDisabledSelectProcessingAndRequest = res.body.checkBookProgressStatusCode === '02' || res.body.checkBookProgressStatusCode === '03' || res.body.checkBookProgressStatusCode === '04' || res.body.checkBookProgressStatusCode === '09' ? true : false
            }
        },

        async handleEdit() {
            const reqBody = {
                checkBookProgressStatusCode: this.chequeRequestDetail.checkBookProgressStatusCode,
                approvalRequestDate: this.$route.query.approvalRequestDate,
                internetBankingApprovalNo: this.chequeRequestDetail.internetBankingApprovalNo,
                remark: this.chequeRequestDetail.remark
            } as DCM20100141_REQ;

            const res = (await this.$request.getRequest("DCM20100141", reqBody, false)) as DCM20100141_RES;
            if (res.header.result) {
                this.isEditable = true;
                this.$dialog.onOpen({
                    content: this.$t("message.update_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        this.requestChequeRequestDetail()
                        modalController.dismiss()
                    }
                });
            }
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case "cancel":
                    this.$dialog.onOpen({
                        content: `${this.$t("message.cancel_without_saving_changes")}`,
                        dialogMode: "Question",
                        callback: () => {
                            this.requestChequeRequestDetail();
                            this.isEditable = true;
                            this.isInValidInput = false;
                        },
                    });
                    break;
                case "save":
                    if (this.chequeRequestDetail.remark && this.chequeRequestDetail.checkBookProgressStatusCode) {
                        this.$dialog.onOpen({
                            content: this.$t("message.would_you_like_to_save"),
                            dialogMode: "Question",
                            callback: () => {
                                this.handleEdit();
                            },
                        });
                    } else {
                        this.isInValidInput = true;
                    }
                    break;
                case "edit":
                    this.isEditable = false;
                    break;
                case 'list':
                    this.$router.push("DCM201001SC00");
                    break;
            }
        },
    },
});
